<template>
  <div>
    <Navbar>
      <template v-slot:left>
        <div class="icon"><i class="iconfont icon-jiantou"></i></div>
      </template>
      <template v-slot:default> 商品详情：{{ id }} </template>
      <template v-slot:right> </template>
    </Navbar>
    <van-image
      style="margin-top: 50px"
      width="100%"
      lazy-load
      :src="detail.cover_url"
    />
    <van-card
      style="text-align: left"
      :num="detail.stock"
      :price="detail.price + '.00'"
      :desc="detail.description"
      :title="detail.title"
    >
      <template #tags>
        <van-tag plain type="danger">新书</van-tag>
        <van-tag plain type="danger">推荐</van-tag>
      </template>
      <template #footer>
        <van-button type="warning" @click="handleAddCart"
          >加入购物车</van-button
        >
        <van-button type="danger" @click="gotoCart">立即购买</van-button>
      </template>
    </van-card>

    <van-tabs v-model="active">
      <van-tab title="概述">
        <div class="content" v-html="detail.details"></div>
      </van-tab>
      <van-tab title="热评">
        <div v-for="item in detail.comments" :key="item.id">
          {{ item }}
        </div>
      </van-tab>
      <van-tab title="相关推荐">
        <Goodslist :goods="like_goods"></Goodslist>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Goodslist from "components/content/goods/Goodslist";
import Navbar from "components/common/navbar/Navbar";
import { getDetail } from "network/detail";
import { useStore } from "vuex";
import { useRoute, useRouter } from "vue-router";
import { ref, onMounted, reactive, toRefs } from "vue";
import { addCart } from "network/cart";
import { Toast } from "vant";

export default {
  name: "Detail",
  components: {
    Navbar,
    Goodslist,
  },
  setup() {
    let active = ref(1);
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    let id = ref(0);
    let book = reactive({
      detail: {},
      like_goods: [],
    });

    onMounted(() => {
      id.value = route.query.id;

      getDetail(id.value).then((res) => {
        book.detail = res.goods;
        book.like_goods = res.like_goods;
      });
    });

    // 添加购物车
    const handleAddCart = () => {
      addCart({ goods_id: book.detail.id, num: 1 }).then((res) => {
        if (res.status == "201" || res.status == "204") {
          Toast.success("添加成功");
          store.dispatch('updataCart');
        }
      });
    };

    // 立即购买
    const gotoCart = () => {
      addCart({ goods_id: book.detail.id, num: 1 }).then((res) => {
        if (res.status == "201" || res.status == "204") {
          Toast.success("添加成功");
          store.dispatch('updataCart');
          router.push({ path: "/cart" });
        }
      });
    };

    return {
      id,
      ...toRefs(book),
      active,
      handleAddCart,
      gotoCart,
      router,
    };
  },
};
</script>

<style lang="scss">
.content {
  padding: 10px;
  img {
    max-width: 100%;
    height: auto;
  }
}
</style>